import React, { Component } from "react";
import { Form, Label, Button, Link} from "react-bootstrap";

export default class AddMemorie extends Component {
  render() {
    return (
      <div>
        <Form onSubmit={this.handelSubmit}>
          <Form.Group>
            <Form.Label>Memories</Form.Label>
            <Form.Control
              type="text"
              name="memorie"
              value={this.state.imgName}
              onChange={this.handleChange}
              placeholder="Upload picture"
            />
          </Form.Group>
          <Form.Group>
            <Form.Control
              type="text"
              name="Artist"
              value={this.state.memorieStory}
              onChange={this.handleChange}
              placeholder="Remember when..."
            />
          </Form.Group>
          <Button type="submit">Add Memorie</Button>
        </Form>
        {/* <Link to="/memories">Go back to memories</Link> */}
      </div>
    );
  }
}
